import React from "react";
import "./index.css";
import axios from 'axios'


export default function App() {

  const [formState,setFormState]=React.useState({
    origin:"",destination:""
  });

  let [responseData, setResponseData] = React.useState("0 km");




  const reqData = {origin:`${formState.origin}`,destination:`${formState.destination}`};


  function handleClick(e){
    e.preventDefault();
    axios.post('http://localhost:5000/api',reqData)
    .then(res => {
        if(res.data.rows[0].elements[0].status==="OK")
          setResponseData(res.data.rows[0].elements[0].distance.text);
        else {
          setResponseData("Enter correct City Name");
        }  
        console.log(res);
    })
    .catch(function (error) {
        console.log(error);
    })
      
  
  }

  function handleChange(e){
    const value = e.target.value;
    setFormState({
      ...formState,
      [e.target.name]: value
    });

  }

 
  return (
    <div className="form-container">
      <form className="register-form">
     
        <input
          id="origin-name"
          className="form-field"
          type="text"
          placeholder="Source"
          name="origin"
          value={formState.origin}
          onChange={handleChange}
        />

        <input
          id="destination-name"
          className="form-field"
          type="text"
          placeholder="Destination"
          name="destination"
          value={formState.destination}
          onChange={handleChange}
        />
  
        <div style={{'textAlign':'center'}}>
          {responseData}
        </div>
        
        <button className="form-field" type="submit" onClick={handleClick}>
          Get Minimum Distance
        </button>
      </form>
    </div>
  );
}